import Vue from 'vue'
import Router from 'vue-router'
import * as Auth from '@/components/pages/Authentication'

// Pages
import Home from '@/components/pages/Home'
import TestEditor from '@/components/pages/TestEditor'
import QuestionEditor from '@/components/pages/QuestionEditor'
import Authentication from '@/components/pages/Authentication/Authentication'

// Global components
import Header from '@/components/components/Header'
import ClickConfirm from '@/components/components/ClickConfirm'
Vue.component('app-header', Header)
Vue.component('click-confirm', ClickConfirm)

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        requiredAuth: false
      }
    },
    {
      path: '/test-editor',
      name: 'TestEditor',
      component: TestEditor,
      meta: {
        requiredAuth: true
      }
    },
    {
      path: '/question-editor',
      name: 'QuestionEditor',
      component: QuestionEditor,
      meta: {
        requiredAuth: true
      }
    },
    {
      path: '/login',
      name: 'Authentication',
      component: Authentication
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.requiredAuth) {
    if (Auth.default.user.authenticated) {
      next()
    } else {
      router.push('/login')
    }
  } else {
    next()
  }
})

export default router



