<template>
  <div>
    <app-header></app-header>

    <!-- Test list view -->
    <b-container v-if="testListViewing">
      <!-- Test list view button group -->
      <b-button-toolbar class="my-2">
        <b-button-group>
          <b-btn size="sm"
                 variant="primary"
                 v-b-modal.addNewTest>Добавить</b-btn>
        </b-button-group>
      </b-button-toolbar>

      <!-- Test list view -->
      <b-table striped
               :items="tests"
               :fields="testTableFields">
        <template slot="title" slot-scope="row">
          <a href="#" @click.stop="testListViewing = !testListViewing; currentTest = JSON.parse(JSON.stringify(row.item))">{{ row.item.title }}</a>
        </template>
        <template slot="actions" slot-scope="row">
          <div class="text-sm-right">
            <b-button size="sm"
                      @click.stop="row.toggleDetails"
                      variant="info">
              <i class="fas fa-info-circle" aria-hidden="true"></i>
            </b-button>
            <click-confirm>
              <b-button size="sm"
                        @click.stop="$store.dispatch('test/DELETE_TEST', row.item)"
                        variant="danger">
                <i class="fas fa-trash" aria-hidden="true"></i>
              </b-button>
            </click-confirm>
          </div>
        </template>
        <template slot="row-details" slot-scope="row">
          <b-card>
            <p>Всего вопросов: {{ row.item.questions.length }}</p>
            <p v-html="row.item.description"></p>
          </b-card>
        </template>
      </b-table>

      <!-- Add new test modal -->
      <b-modal id="addNewTest"
               @ok="$store.dispatch('test/ADD_TEST_TITLE_ONLY', newTestTitle)"
               @show="newTestTitle = ''"
               ok-title="Сохранить"
               cancel-title="Отмена"
               title="Добавить новый тест">
        <b-form>
          <b-input-group size="sm"
                         prepend="Название">
            <b-form-input v-model="newTestTitle"></b-form-input>
          </b-input-group>
        </b-form>
      </b-modal>
    </b-container>

    <!-- Test composer view -->
    <b-container v-if="!testListViewing">
      <!-- Test composer button group -->
      <b-button-toolbar class="my-2">
        <b-button-group size="sm">
          <b-btn variant="primary"
                 @click="$store.dispatch('test/SAVE_TEST', currentTest)">
            Сохранить
          </b-btn>
          <b-btn variant="outline-secondary"
                 @click="testListViewing = true; currentTest = null">
            Назад
          </b-btn>
        </b-button-group>
      </b-button-toolbar>

      <b-container fluid>
        <h3>{{ currentTest.title }}</h3>
          <vue-editor v-model="currentTest.description"
                      :editorToolbar="customToolbar"
                      id="description-editor"></vue-editor>
        <b-row>
          <b-col cols="8">
            <b-list-group>
              <h5>Вопросы в тесте:</h5>
              <b-list-group-item v-for="question in currentTest.questions"
                                 :key="question.id"
                                 @click.stop="$store.commit('test/DELETE_QUESTION', { test: currentTest, question })"
                                 class="question px-2 py-2 d-flex justify-content-between align-items-center">
                <div>
                  <span v-html="question.question"></span>
                  <small class="text-muted">{{ question.question_pack.title }}</small>
                </div>
                <b-badge pill variant="primary">{{ question.type }}</b-badge>
              </b-list-group-item>
            </b-list-group>
          </b-col>

          <b-col>
            <b-form-group label="Добавить вопросы из сборника:"
                          label-for="question-pack-selector">
              <b-form-select v-model="selectedQuestonPack"
                            :options="question_pack_options"
                            id="question-pack-selector"
                            size="sm"></b-form-select>
            </b-form-group>
            <b-list-group class="mt-2">
              <b-list-group-item v-for="question in questionsOfSelectedQuestionPack"
                                 :key="question._id"
                                 @click.stop="$store.commit('test/ADD_QUESTION', { test: currentTest, question })"
                                 class="question px-2 py-2 d-flex justify-content-between align-items-center"
                                 :variant="questionAlreadyInTest(question) ? 'success' : ''">
                <span v-html="question.question"></span>
                <b-badge pill variant="primary">{{ question.type }}</b-badge>
              </b-list-group-item>
            </b-list-group>
          </b-col>
        </b-row>
      </b-container>
    </b-container>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      testListViewing: true,
      currentTest: null,
      newTestTitle: '',

      selectedQuestonPack: null,

      testTableFields: [
        { key: 'title', label: 'Название' },
        { key: 'author.username', label: 'Автор' },
        { key: 'actions', label: 'Действия' }
      ],
      customToolbar: [
        ['bold', 'italic', 'underline'],
        [{ 'list': 'ordered' }, { 'list': 'bullet' }],
        ['code-block']
      ]
    }
  },

  computed: {
    question_pack_options () {
      let options = []
      for (let item of this.question_packs) {
        options.push({
          value: item,
          text: item.title
        })
      }
      return options
    },

    questionsOfSelectedQuestionPack () {
      if (this.selectedQuestonPack !== null) return this.questions.filter(question => question.question_pack._id === this.selectedQuestonPack._id)
      else return null
    },

    ...mapGetters({
      'tests': 'test/tests',
      'questions': 'questions/questions',
      'question_packs': 'questionPack/questionPacks'
    })
  },

  mounted () {
    this.$store.dispatch('test/GET_TESTS')
    this.$store.dispatch('questionPack/GET_QUESTION_PACKS')
    this.$store.dispatch('questions/GET_ALL_QUESTIONS')
  },

  methods: {
    questionAlreadyInTest (question) {
      let index = this.currentTest.questions.findIndex((element) => {
        return element._id === question._id
      })
      return index !== -1
    }
  }
}
</script>
<style>
.question {
  font-size: smaller;
}
.question p {
  margin-bottom: 2px;
}
</style>



