import * as actions from './actions'
import getters from './getters'

const state = {
  questions: []
}

const mutations = {
  SET_QUESTIONS: (state, list) => { state.questions = list },

  DELETE_QUESTION: (state, question) => {
    let index = state.questions.indexOf(question)
    if (index !== -1) state.questions.splice(index, 1)
  },

  ADD_QUESTION: (state, question) => { state.questions.push(question) },

  SAVE_QUESTION: (state, question) => {
    let index = state.questions.findIndex((element) => {
      return element._id === question._id
    })
    if (index !== -1) state.questions.splice(index, 1, question)
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}



