import * as actions from './actions'
import getters from './getters'

const state = {
  tests: []
}

const mutations = {
  SET_TESTS: (state, list) => { state.tests = list },

  ADD_TEST: (state, test) => { state.tests.push(test) },

  ADD_QUESTION: (state, { test, question }) => {
    let index = test.questions.findIndex((element) => {
      return element._id === question._id
    })
    if (index === -1) test.questions.push(question)
  },

  DELETE_QUESTION: (state, { test, question }) => {
    let index = test.questions.indexOf(question)
    if (index !== -1) test.questions.splice(index, 1)
  },

  SAVE_TEST: (state, test) => {
    let index = state.tests.findIndex((element) => {
      return element._id === test._id
    })
    if (index !== -1) state.tests.splice(index, 1, test)
  },

  DELETE_TEST: (state, test) => {
    let index = state.tests.indexOf(test)
    if (index !== -1) state.tests.splice(index, 1)
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}



