import Axios from 'axios'
import Authentication from '@/components/pages/Authentication'

const url = process.env.NODE_ENV === 'production' ? 'https://testingmanager.herokuapp.com' : `http://${window.location.hostname}:3001`

var axios = Axios.create({
  baseURL: url
})
axios.defaults.headers.common['Authorization'] = Authentication.getAuthenticationHeader(this)

export default axios



