import axios from '@/store/axios'

export function GET_QUESTIONS ({ commit }, item) {
  axios.get('/api/v1/question', { params: { question_pack: item._id } })
    .then(({data}) => {
      commit('SET_QUESTIONS', data)
    })
}

export function GET_ALL_QUESTIONS ({ commit }) {
  axios.get('/api/v1/question')
    .then(({data}) => {
      commit('SET_QUESTIONS', data)
    })
}

export function ADD_QUESTION ({ commit }, question) {
  return axios.post('/api/v1/question', question)
    .then(({data}) => {
      commit('ADD_QUESTION', data.question)
      return data
    })
}

export function SAVE_QUESTION ({ commit }, question) {
  return axios.put('/api/v1/question/single', question)
    .then(({data}) => {
      commit('SAVE_QUESTION', data.question)
      return data
    })
}

export function DELETE_QUESTION ({ commit }, question) {
  axios.delete('/api/v1/question', { params: { question_id: question._id } })
    .then(({data}) => {
      commit('DELETE_QUESTION', question)
    })
}



