import axios from '@/store/axios'

export function GET_QUESTION_PACKS ({ commit }) {
  axios.get('/api/v1/question_pack')
    .then(({ data }) => {
      commit('SET_QUESTION_PACKS', data)
    })
}

export function ADD_QUESTION_PACK ({ commit }, title) {
  axios.post('/api/v1/question_pack', { 'title': title })
    .then(({ data }) => {
      commit('ADD_QUESTION_PACK', data.question_pack)
    })
}

export function SAVE_QUESTION_PACK ({ commit }, pack) {
  axios.put('/api/v1/question_pack/single', { '_id': pack._id, 'title': pack.title })
}

export function DELETE_QUESTION_PACK ({ commit }, pack) {
  axios.delete('/api/v1/question_pack', { params: { question_pack_id: pack._id } })
    .then(({data}) => {
      commit('DELETE_QUESTION_PACK', pack)
    })
}



