<template>
  <div>
    <!-- Question pack button group -->
    <b-button-toolbar v-if="!questionsEditing" class="my-2">
      <b-button-group>
        <b-btn size="sm"
               v-b-modal.addQuestionPackModal
               variant="primary">Добавить</b-btn>
      </b-button-group>
    </b-button-toolbar>

    <!-- Questions view button group -->
    <b-button-toolbar v-if="questionsEditing" class="my-2">
      <b-button-group>
        <b-btn size="sm"
               @click="showQuestionEditorModal(null)"
               variant="primary">
          Добавить новый вопрос
        </b-btn>
        <b-btn size="sm"
               variant="outline-secondary"
               @click="questionsEditing = false">
          Назад
        </b-btn>
      </b-button-group>
    </b-button-toolbar>

    <!-- Question packs table -->
    <b-table striped
             :fields="fields"
             :items="question_packs"
             v-if="!questionsEditing">
      <template slot="title" slot-scope="row">
        <a href="#" @click="questionsEditing = !questionsEditing; getQuestionsForEditor(row.item)">{{ row.item.title }}</a>
      </template>
      <template slot="actions" slot-scope="row">
        <div class="text-sm-right">
          <b-button size="sm"
                    @click.stop="row.toggleDetails"
                    variant="info">
            <i class="fas fa-info-circle" aria-hidden="true"></i>
          </b-button>
          <click-confirm>
            <b-button size="sm"
                      @click.stop="$store.dispatch('questionPack/DELETE_QUESTION_PACK', row.item)"
                      variant="danger">
              <i class="fas fa-trash" aria-hidden="true"></i>
            </b-button>
          </click-confirm>
        </div>
      </template>
      <template slot="row-details" slot-scope="row">
        <b-card>
          <b-row class="mb-2">
            <b-col sm="3" class="text-sm-right"><b>Вопросов:</b></b-col>
            <b-col sm="3" v-text="questionSetCount(row.item._id)"></b-col>
            <b-col class="text-sm-right col-sm">
              <!-- !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
              !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
              !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! -->
              <b-button size="sm" @click="row.item.editing = !row.item.editing"><i class="fas fa-edit" aria-hidden="true"></i></b-button>
            </b-col>
          </b-row>
          <b-row v-if="row.item.editing">
            <b-input-group prepend="Новое название">
              <b-form-input v-model="row.item.title"></b-form-input>
              <b-input-group-append>
                <b-btn variant="success" @click="saveQuestionPack(row.item)"><i class="far fa-save"></i></b-btn>
              </b-input-group-append>
            </b-input-group>
          </b-row>
        </b-card>
      </template>
    </b-table>

    <!-- Questions table -->
    <b-table striped
             :fields="questionEditorFields"
             :items="curentQuestionSet"
             v-else-if="questionsEditing">
      <template slot="question" slot-scope="row">
        <div class="pr-2">
          <a href="#"
             @click="showQuestionEditorModal(row.item)"
             v-html="row.item.question"
             class="question">
          </a>
          <b-badge pill>{{ row.item.type }}</b-badge>
        </div>
      </template>
      <template slot="actions" slot-scope="row">
        <div class="text-sm-right">
          <b-button size="sm"
                    @click.stop="row.toggleDetails"
                    variant="info">
            <i class="fas fa-info-circle" aria-hidden="true"></i>
          </b-button>
          <click-confirm>
            <b-button size="sm"
                      @click.stop="$store.dispatch('questions/DELETE_QUESTION', row.item)"
                      variant="danger">
              <i class="fas fa-trash" aria-hidden="true"></i>
            </b-button>
          </click-confirm>
        </div>
      </template>
      <template slot="row-details" slot-scope="row">
        <question-card :question="row.item"></question-card>
      </template>
    </b-table>

    <!-- Add question pack modal -->
    <b-modal id="addQuestionPackModal"
             ref="addQuestionPackModal"
             title="Добавить новый сборник вопросов"
             ok-title="Сохранить"
             cancel-title="Отмена"
             @ok="handleAddQuestionPackSubmit"
             @show="newQuestionPackName = ''"
             v-if="!questionsEditing">
      <b-form @submit.stop.prevent="handleAddQuestionPackSubmit">
        <b-input-group size="sm" prepend="Название">
          <b-form-input v-model="newQuestionPackName"></b-form-input>
        </b-input-group>
      </b-form>
    </b-modal>

    <!-- Add/edit question modal -->
    <b-modal id="addEditQuestionModal"
             ref="addEditQuestionModal"
             title="Редактирование вопроса"
             v-if="questionsEditing"
             ok-title="Сохранить"
             cancel-title="Отмена"
             @ok="() => { this.$refs.questionEditor.addSaveQuestion() }"
             size="lg">
      <question-editor :question_pack_id="questionPackForEdit._id"
                       ref="questionEditor"
                       :question="newQuestion">
      </question-editor>
    </b-modal>
  </div>
</template>
<script>
import QuestionCard from './QuestionCard'
import QuestionEditor from './QuestionEditor'
import { mapGetters } from 'vuex'

const defaultQuestion = {
  question: '',
  type: 'выбор',
  choise_answers: [
    { answer: '', correct: false },
    { answer: '', correct: false }
  ]
}

export default {
  components: {
    'question-card': QuestionCard,
    'question-editor': QuestionEditor
  },

  data () {
    return {
      newQuestionPackName: '',

      needUpdateQuestionCounts: false,
      questionsEditing: false,
      questionPackForEdit: {},
      questionEditorFields: [
        { key: 'question', label: 'Вопрос' },
        { key: 'actions', label: 'Действия' }
      ],
      newQuestion: JSON.parse(JSON.stringify(defaultQuestion))
    }
  },

  props: ['fields'],

  computed: {
    curentQuestionSet () {
      return this.questions.filter(question => question.question_pack._id === this.questionPackForEdit._id)
    },
    ...mapGetters({
      'question_packs': 'questionPack/questionPacks',
      'questions': 'questions/questions',
      'questionSetCount': 'questions/questionSetCount'
    })
  },

  mounted () {
    this.$store.dispatch('questionPack/GET_QUESTION_PACKS')
    this.$store.dispatch('questions/GET_ALL_QUESTIONS')
  },

  methods: {
    saveQuestionPack (item) {
      this.$store.dispatch('questionPack/SAVE_QUESTION_PACK', item)
      item.editing = false
    },

    addQuestionPack () {
      this.$store.dispatch('questionPack/ADD_QUESTION_PACK', this.newQuestionPackName)
      this.$refs.addQuestionPackModal.hide()
    },

    handleAddQuestionPackSubmit (event) {
      event.preventDefault()
      if (!this.newQuestionPackName) alert('Поле не должно быть пустым')
      else this.addQuestionPack()
    },

    getQuestionsForEditor (item) {
      if (this.questionPackForEdit !== item || this.needUpdateQuestionCounts) this.questionPackForEdit = item
    },

    showQuestionEditorModal (question) {
      if (!question) this.newQuestion = JSON.parse(JSON.stringify(defaultQuestion))
      else {
        this.newQuestion = JSON.parse(JSON.stringify(question))
        if (this.newQuestion.type === 'выбор') this.newQuestion.choise_answers = this.newQuestion.answers
      }
      this.$refs.addEditQuestionModal.show()
    }
  }
}
</script>
<style>
a.question p{
  display: inline-block;
  margin-bottom: 0;
}
</style>



