import Axios from 'axios'
import router from '@/router'
const TestManagerAPI = process.env.NODE_ENV === 'production' ? 'https://testingmanager.herokuapp.com' : `http://${window.location.hostname}:3001`

export default {
  user: { authenticated: false },

  authenticate (context, credentials, redirect) {
    Axios
      .post(`${TestManagerAPI}/api/v1/auth`, credentials)
      .then(({ data }) => {
        context.$cookies.set('token', data.token, '1D')
        context.$cookies.set('user_id', data.user._id, '1D')
        context.validLogin = true

        this.user.authenticated = true

        if (redirect) router.push(redirect)
      })
      .catch(({ response: {data} }) => {
        context.snackbar = true
        context.message = data.message
      })
  },

  signup (context, credentials, redirect) {
    Axios
      .post(`${TestManagerAPI}/api/v1/signup`, credentials)
      .then(() => {
        context.validSignUp = true

        this.authenticate(context, credentials, redirect)
      })
      .catch(({ response: {data} }) => {
        context.snackbar = true
        context.message = data.message
      })
  },

  signout (context, redirect) {
    window.$cookies.remove('token')
    window.$cookies.remove('user_id')
    this.user.authenticated = false

    if (redirect) router.push(redirect)
  },

  checkAuthentication () {
    const token = document.cookie

    this.user.authenticated = !!token
  },

  getAuthenticationHeader (context) {
    return `Bearer ${window.$cookies.get('token')}`
  }
}



