<template>
  <div>
    <b-row>
      <b-col cols="9">
        <vue-editor v-model="question.question"
                    :editorToolbar="customToolbar"></vue-editor>
      </b-col>
      <b-col>
        <b-form-group label="Тип вопроса">
          <b-form-select v-model="question.type"
                        :options="questionTypes"></b-form-select>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row class="mt-2">
      <b-col>Ответ(ы):</b-col>
    </b-row>
    <!-- Выбор ответа -->
    <div v-if="question.type == 'выбор'">
      <b-input-group v-for="(answer, index) in question.choise_answers"
                     :key="index"
                     class="mt-2">
        <b-input-group-prepend is-text>
          <b-form-checkbox v-model="answer.correct"
                           plain
                           class="mr-0"></b-form-checkbox>
        </b-input-group-prepend>
        <b-form-input type="text"
                      size="sm"
                      v-model="answer.answer"></b-form-input>
        <b-input-group-append>
          <b-btn size="sm"
                 @click="() => {if (index !== -1) question.choise_answers.splice(index, 1)}"
                 v-if="index != question.choise_answers.length - 1"
                 variant="danger">
            <i class="fas fa-minus"></i>
          </b-btn>
          <b-btn size="sm"
                 @click="question.choise_answers.push({ answer: '', correct: false })"
                 v-if="index == question.choise_answers.length - 1"
                 variant="success">
            <i class="fas fa-plus"></i>
          </b-btn>
        </b-input-group-append>
      </b-input-group>
    </div>

    <div v-else-if="question.type == 'ввод ответа'">
      {{ question.type }}
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      questionTypes: [ 'выбор', 'ввод ответа' ],
      customToolbar: [
        ['bold', 'italic', 'underline'],
        [{ 'list': 'ordered' }, { 'list': 'bullet' }],
        ['code-block']
      ]
    }
  },

  props: [ 'question_pack_id', 'question' ],

  methods: {
    addSaveQuestion () {
      if (this.question.type === 'выбор') this.question.answers = this.question.choise_answers
      var newQuestion = {
        '_id': this.question._id,
        'question': this.question.question,
        'type': this.question.type,
        'question_pack': this.question_pack_id,
        'answers': this.question.answers
      }

      if (!this.question._id) this.$store.dispatch('questions/ADD_QUESTION', newQuestion)
      else this.$store.dispatch('questions/SAVE_QUESTION', newQuestion)
    }
  }
}
</script>



