import axios from '@/store/axios'

export function GET_TESTS ({commit}) {
  axios.get('/api/v1/test')
    .then(({data}) => {
      commit('SET_TESTS', data)
    })
}

export function ADD_TEST ({commit}, test) {
  axios.post('/api/v1/test', test)
    .then(({data}) => {
      commit('ADD_TEST', data.test)
    })
}

export function ADD_TEST_TITLE_ONLY ({commit}, title) {
  const test = {
    title,
    description: '',
    questions: [],
    author: null
  }
  ADD_TEST({commit}, test)
}

export function SAVE_TEST ({commit}, test) {
  axios.put('/api/v1/test/single', test)
    .then(({data}) => {
      commit('SAVE_TEST', data.test)
    })
}

export function DELETE_TEST ({commit}, test) {
  axios.delete('/api/v1/test', { params: { test_id: test._id } })
    .then(({data}) => {
      commit('DELETE_TEST', test)
    })
}



