import Vue from 'vue'
import * as actions from './actions'
import getters from './getters'

const state = {
  questionPacks: []
}

const mutations = {
  SET_QUESTION_PACKS: (state, list) => {
    state.questionPacks = list
    for (var item of state.questionPacks) {
      Vue.set(item, 'editing', false)
      Vue.set(item, 'questions', undefined)
    }
  },

  ADD_QUESTION_PACK: (state, pack) => {
    Vue.set(pack, 'editing', false)
    Vue.set(pack, 'questions', undefined)
    state.questionPacks.push(pack)
  },

  DELETE_QUESTION_PACK: (state, pack) => {
    let index = state.questionPacks.indexOf(pack)
    if (index !== -1) state.questionPacks.splice(index, 1)
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}



