<template>
  <b-card>
    <b-card-header v-html="question.question"></b-card-header>
    <div v-if="question.type='выбор'">
      <b-list-group>
        <b-list-group-item v-for="answer in question.answers"
                           :key="answer._id"
                           :variant="answer.correct ? 'success' : ''">
          {{ answer.answer }}
        </b-list-group-item>
      </b-list-group>
    </div>
  </b-card>
</template>
<script>
export default {
  props: ['question']
}
</script>



